import React, { useCallback, useEffect } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { Row, Col, Typography, Card } from "antd";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import {
  getProjectSprintPool,
  moveItemInAllPool
} from "../../../action/project";
import { clearErr } from "../../../action/resource";
import useTitle from "../../util/useTitle";
import MyModal from "../../util/WarnModal";

const { Title } = Typography;

const grid = 8;

const getItemStyle = (isDragging, draggableStyle) => ({
  transition: "all .7s",

  userSelect: "none",
  padding: "6px",
  margin: `${grid / 2}px`,
  marginBottom: `${grid}px`,
  borderRadius: "8px",
  boxShadow: "rgba(240,240,240,.6) 0px 0px 4px 0px",
  fontWeight: "700",

  background: isDragging ? "lightgreen" : "rgba(140,140,140,1)",
  // width: isDragging ? '40px' : 'auto',

  ...draggableStyle
});

const getListStyle = isDraggingOver => ({
  transition: "all .7s",
  background: isDraggingOver ? "#666" : "#222",
  boxShadow: isDraggingOver
    ? "green 0px 0px 8px 0px"
    : "rgba(147, 142, 142, 0.38) 0px 0px 7px 0px",
  margin: "16px auto",
  padding: grid,
  borderRadius: "16px",
  border: isDraggingOver ? "2px solid green" : "2px solid transparent",
  minHeight: "150px",
  // padding: grid,
  display: "flex",
  width: "70%",
  maxWidth: "250px",
  flexDirection: "column"
});

const getPoolListStyle = isDraggingOver => ({
  transition: "all .7s",
  background: isDraggingOver ? "#666" : "#222",
  boxShadow: isDraggingOver
    ? "green 0px 0px 8px 0px"
    : "rgba(40, 142, 142, 0.38) 0px 0px 7px 0px",
  margin: "16px",
  padding: grid,
  border: isDraggingOver ? "2px solid green" : "2px solid transparent",
  borderRadius: "16px",
  minHeight: "300px",
  // overflowY: 'auto',
  // border: '1px solid ',
  // maxWidth: '0px',
  width: "90%",
  display: "flex",
  flexDirection: "column"
});

const App = props => {
  useTitle("project pool");
  const dispatch = useDispatch();
  const getAllSprintPool = useCallback(
    () => dispatch(getProjectSprintPool(props.match.params.projectId)),
    [dispatch, props.match.params.projectId]
  );

  useEffect(() => {
    getAllSprintPool();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.match.params.projectId]);
  const { Pool, Others, loading } = useSelector(
    state => state.allSprintPool,
    shallowEqual
  );
  const err = useSelector(state => state.err, shallowEqual);

  const clearErrCallback = useCallback(
    () => dispatch(clearErr()),
    [dispatch]
  );

  useEffect(() => {
    if (Object.keys(err).length) {
      MyModal({ data: err });
      clearErrCallback();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [err]);

  const onDragEnd = result => {
    const { source, destination } = result;
    if (!destination) {
      return;
    }
    const [sName, sId] = source.droppableId.split(" ", 2);
    const [dName, dId] = destination.droppableId.split(" ", 2);
    dispatch(
      moveItemInAllPool(
        props.match.params.projectId,
        result.draggableId,
        sName,
        sId,
        source.index,
        dName,
        dId,
        destination.index
      )
    );
  };

  return loading ? (
    <div>loading</div>
  ) : Others.length !== 0 ? (
    <Card style={{ height: "" }}>
      <DragDropContext onDragEnd={onDragEnd}>
        <Row style={{ width: "100%", margin: "0 auto", height: "100%" }}>
          <Col xs={24} sm={10} md={8} lg={6}>
            <Droppable droppableId={`pool ${Pool.sprintId}`}>
              {(provided, snapshot) => (
                <div
                  ref={provided.innerRef}
                  style={getPoolListStyle(snapshot.isDraggingOver)}
                >
                  <Title
                    style={{
                      background: "rgb(90, 90, 90)",
                      textAlign: "center",
                      borderRadius: "16px",
                      marginBottom: "8px",
                      marginTop: "8px",
                      paddingBottom: "4px",
                      paddingTop: "4px"
                    }}
                    level={4}
                  >
                    {Pool.sprintName}
                  </Title>
                  {Pool.stories.map((item, index) => (
                    <Draggable
                      draggableId={item.storyId}
                      key={item.storyId}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          {item.storyName}
                        </div>
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </Col>

          <Col
            xs={24}
            md={12}
            lg={16}
            style={{
              marginBottom: "8px",
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "start"
            }}
          >
            {Others.map(Other => (
              <Droppable
                key={Other.sprintId}
                droppableId={`other ${Other.sprintId}`}
              >
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    style={getListStyle(snapshot.isDraggingOver)}
                  >
                    <Title
                      style={{
                        background: "rgb(90, 90, 90)",
                        textAlign: "center",
                        borderRadius: "16px",
                        marginBottom: "8px",
                        marginTop: "8px",
                        paddingBottom: "4px",
                        paddingTop: "4px",
                        width: "100%"
                      }}
                      level={4}
                    >
                      {Other.sprintName}
                    </Title>
                    {Other.stories.map((item, index) => (
                      <Draggable
                        key={item.storyId}
                        draggableId={item.storyId}
                        index={index}
                      >
                        {(provided, snapshot) => (
                          <div
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            style={getItemStyle(
                              snapshot.isDragging,
                              provided.draggableProps.style
                            )}
                          >
                            {item.storyName}
                          </div>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            ))}
          </Col>
        </Row>{" "}
      </DragDropContext>
    </Card>
  ) : (
        <h2 style={{ textAlign: "center" }}>Please Define a Sprint</h2>
      );
};

export default App;
